.product {
  display: flex;
  flex-direction: column;
  max-height: 400px;
  min-width: 100px;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  margin: 10px;
  padding: 20px;
  z-index: 1;
  background-color: white;
}

.product_rating {
  display: flex;
}
.product_Price {
  margin-top: 5px;
}
.product_info {
  height: 100px;
  margin-bottom: 15px;
}
.product > img {
  object-fit: contain;
  width: 100%;
  max-height: 200px;
  margin-bottom: 15px;
  margin-top: 20px;
}

.product > button {
  background-color: #f3a847;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
}
